import React, { useEffect } from 'react';
import './clock.scss';

export default () => {
  useEffect(() => {
    setInterval(setClock, 1000)

    const hourHand = document.querySelector('[data-hour-hand]');
    const minuteHand = document.querySelector('[data-minute-hand]');
    const secondHand = document.querySelector('[data-second-hand]');
    const digital = document.querySelector('[data-digital]');
    function setClock() {
      const currentDate = new Date();
      const hours = currentDate.getHours() < 10 ? `0${currentDate.getHours()}` : currentDate.getHours();
      const minutes = currentDate.getMinutes() < 10 ? `0${currentDate.getMinutes()}` : currentDate.getMinutes();
      const seconds = currentDate.getSeconds() < 10 ? `0${currentDate.getSeconds()}` : currentDate.getSeconds();

      const formattedTime = `${hours} : ${minutes} : ${seconds}`;

      const secondsRatio = currentDate.getSeconds() / 60
      const minutesRatio = (secondsRatio + currentDate.getMinutes()) / 60
      const hoursRatio = (minutesRatio + currentDate.getHours()) / 12
      setRotation(secondHand, secondsRatio);
      setRotation(minuteHand, minutesRatio);
      setRotation(hourHand, hoursRatio);
      digital.innerHTML = formattedTime;


      // check chrome console open
      var element = new Image();
      Object.defineProperty(element, 'id', {
        get: function () {
          /* TODO */
          alert('Please Close this console to remove this message');
          return true;
        }
      });
      console.log('%cHello', element);
    }

    function setRotation(element, rotationRatio) {
      element.style.setProperty('--rotation', rotationRatio * 360)
    }

    setClock()
  }, [])
  return (
    <>
      <div className="clock">
        <div className="hand hour" data-hour-hand></div>
        <div className="hand minute" data-minute-hand></div>
        <div className="hand second" data-second-hand></div>
        <div className="number number1">1</div>
        <div className="number number2">2</div>
        <div className="number number3">3</div>
        <div className="number number4">4</div>
        <div className="number number5">5</div>
        <div className="number number6">6</div>
        <div className="number number7">7</div>
        <div className="number number8">8</div>
        <div className="number number9">9</div>
        <div className="number number10">10</div>
        <div className="number number11">11</div>
        <div className="number number12">12</div>
      </div>
      <div className="digital" data-digital></div>
    </>
  )
}
